'use client'
import { useForm, Controller } from 'react-hook-form'
import { zodResolver } from '@hookform/resolvers/zod'
import * as z from 'zod'
import toast from 'react-hot-toast'
import { useRouter } from 'next/navigation'
import FormInput from '@/components/FormInput'
import FileUpload from '@/components/FileUpload'

const schema = z.object({
  rsa_balance: z.string().min(1, 'Required'),
  bvn: z.string().length(11, 'BVN must be 11 digits'),
  nin: z.string().length(11, 'NIN must be 11 digits'),
  dob: z.string(),
  phone1: z.string().min(1, 'Required'),
  phone2: z.string().optional(),
  alt_email: z.string().email('Invalid email'),
  pfa: z.string().min(1, 'Required'),
  // Add validations for documents if needed
})

type FormData = z.infer<typeof schema>

export default function ApplyPage() {
  const {
    register,
    handleSubmit,
    control,
    formState: { errors },
  } = useForm<FormData>({
    resolver: zodResolver(schema),
  })

  const router = useRouter()

  const onSubmit = async (data: FormData) => {
    try {
      // Handle upload + data submission
      toast.success('Application submitted!')
    } catch (err) {
      toast.error('Submission failed')
    }
  }

  return (
    <form onSubmit={handleSubmit(onSubmit)} className="max-w-2xl mx-auto">
      <FormInput label="RSA Balance" {...register('rsa_balance')} error={errors.rsa_balance?.message} />
      <FormInput label="BVN" {...register('bvn')} error={errors.bvn?.message} />
      <FormInput label="NIN" {...register('nin')} error={errors.nin?.message} />
      <FormInput label="Date of Birth" type="date" {...register('dob')} error={errors.dob?.message} />
      <FormInput label="Phone 1" {...register('phone1')} error={errors.phone1?.message} />
      <FormInput label="Phone 2" {...register('phone2')} error={errors.phone2?.message} />
      <FormInput label="Alt Email" type="email" {...register('alt_email')} error={errors.alt_email?.message} />

      {/* Add PFA dropdown, and FileUploads using Controller if needed */}

      <button type="submit" className="btn btn-primary mt-4">
        Submit
      </button>
    </form>
  )
}
